// SB Radio Variables:
// --sb-radio-margin-top
// --sb-radio-label-line-height
// --sb-radio-label-font-weight
// --sb-radio-label-font-size
// --sb-radio-helper-font-size
// --sb-radio-helper-line-height
// --sb-radio-helper-color

.sb-radio__list-item {
  margin-bottom: 0.5rem;
}

.sb-radio__label {
  align-items: flex-start;
  cursor: pointer;
  display: flex;
  flex-direction: row;
}

.sb-radio__input {
  margin-right: var(--sb-radio-margin-right, 8px);
  margin-top: var(--sb-radio-margin-top, 1px);
}

.sb-radio__label-text {
  color: var(--sbt-theme-tertiary-color-90);
  font-size: var(--sb-radio-label-font-size, var(--sbt-font-size-30));
  font-weight: var(--sb-radio-label-font-weight, var(--sbt-font-weight-40));
  grid-area: labelText;
  line-height: var(--sb-radio-label-line-height, var(--sbt-line-height-ui));
}

.sb-radio__helper-text {
  color: var(--sb-radio-helper-color, var(--sbt-theme-tertiary-color-40));
  font-size: var(--sb-radio-helper-font-size, var(--sbt-font-size-25));
  grid-area: helperText;
  line-height: var(--sb-radio-helper-line-height, var(--sbt-line-height-25));
}

.sb-radio_error .sb-radio__label {
  color: var(--sbt-color-error-50);
}
